.btn-normal span {
	-webkit-transform: translateX(20px);
	transform: translateX(20px);
	opacity: 0;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
	-webkit-transition: .3s all linear;
	transition: .3s all linear
}
.btn-normal:hover span {
	-webkit-transform: translateX(0);
	transform: translateX(0);
	visibility: visible;
	opacity: 1;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)"
}
.btn-normal-rev span {
	-webkit-transform: translateX(0);
	transform: translateX(0);
	opacity: 1;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
	-webkit-transition: .3s all linear;
	transition: .3s all linear
}
.btn-normal-rev:hover span {
	-webkit-transform: translateX(20px);
	transform: translateX(20px);
	visibility: hidden;
	opacity: 0;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)"
}
.btn.style-1 {
	position: relative;
	-webkit-transition: .6s all linear;
	transition: .6s all linear
}
.btn.style-1:after {
	content: '';
	height: 10px;
	width: 10px;
	background: #000;
	left: 49%;
	top: 44%;
	-webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition: .3s all linear;
	transition: .3s all linear;
	visibility: hidden;
	opacity: 0;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
	position: absolute;
	border-radius: 50%
}
.btn.style-2:before, .btn.style-3:before {
	left: 48%;
	top: 48%;
	height: 10px;
	width: 10px;
	-webkit-transition: .3s all linear;
	content: ""
}
.btn.style-1:hover:after {
	-webkit-transform: scale(20);
	transform: scale(20);
	opacity: .5;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
	visibility: visible;
	z-index: -1;
	background: 0 0
}
.btn.style-1:hover {
	background: #fff;
	color: #00c6c6!important
}
.btn.style-2 {
	border-color: #ed1c24!important;
	color: #ed1c24!important;
	position: relative
}
.btn.style-3, .style-4 {
	color: #002197!important
}
.btn.style-2:before {
	position: absolute;
	background: #e99614;
	transition: .3s all linear;
	opacity: 0;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
	visibility: hidden
}
.style-2:hover:before {
	opacity: .3;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=30)";
	visibility: visible;
	-webkit-transform: scale(20);
	transform: scale(20)
}
.btn.style-3 {
	border-color: #41bcfe!important;
	position: relative
}
.btn.style-3:before {
	position: absolute;
	background: #002197;
	transition: .3s all linear;
	opacity: 0;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
	visibility: hidden
}
.btn.style-3:hover:before {
	opacity: 1;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
	visibility: visible;
	-webkit-transform: scale(20);
	transform: scale(20)
}
.style-4 {
	border: 2px solid #002197!important;
	position: relative
}
.style-4:after {
	height: 100%;
	width: 100%;
	content: "";
	position: absolute;
	background: #41bcfe;
	left: 0;
	top: 0;
	border-radius: 5px;
	z-index: -1;
	opacity: .1;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=10)";
	-webkit-transition: .3s all linear;
	transition: .3s all linear
}
.style-4:hover:after {
	width: 80%;
	left: 10%
}
